<template>
  <div id="weather">
    <mt-header fixed title="本地天气"></mt-header>
    <div class="alert">
      <van-notice-bar
        text="通知内容"
        background="#ecf9ff"
        left-icon="https://dss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=1042768786,526816433&fm=26&gp=0.jpg"
      >
        <iframe
          name="weather_inc"
          src="http://i.tianqi.com/index.php?c=code&id=1"
          style="margin: 40px auto -50px; padding: 0 15px"
          width="340"
          height="35"
          frameborder="0"
          marginwidth="0"
          marginheight="0"
          scrolling="no"
        ></iframe>
      </van-notice-bar>
    </div>
    <div v-if="life.info != undefined">
      <van-cell-group title="紫外线">
        <van-cell v-cloak title="强度" :value="life.info.ziwaixian[0]" />
        <van-cell v-cloak title="建议" :value="life.info.ziwaixian[1]" />
      </van-cell-group>
      <van-cell-group title="运动">
        <van-cell v-cloak title="适宜度" :value="life.info.yundong[0]" />
        <van-cell v-cloak title="建议" :value="life.info.yundong[1]" />
      </van-cell-group>
    </div>
    <div v-if="pm.pm25 != undefined">
      <van-cell-group title="PM2.5">
        <van-cell v-cloak title="PM值" :value="pm.pm25.curPm" />
        <van-cell v-cloak title="空气" :value="pm.pm25.quality" />
        <van-cell v-cloak title="等级" :value="pm.pm25.level" />
        <van-cell v-cloak title="建议" :value="pm.pm25.des" />
      </van-cell-group>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      life: {},
      pm: {},
      show: false
    };
  },
  mounted() {
    const url =
      "/weather/Query?key=aa0dfe70f1e5437faf53bf77df8f664c&cityname=广州";
    this.axios.get(url).then(res => {
      console.log(res.data.result);
      this.life = res.data.result.life;
      this.pm = res.data.result.pm25;
    });
  },
  beforeRouteEnter(to, from, next) {
    next();
  },
  beforeRouteLeave(to, from, next) {
    // console.log(to);
    if (to.name === "index") {
      // console.log(to.meta.keepAlive);
      to.meta.keepAlive = true;
    }
    next();
  },
  methods: {
    showPopup() {
      this.show = true;
    }
  }
};
</script>
<style scoped>
.alert {
  margin-top: 40px;
  margin-bottom: 40px;
}
[v-cloak] {
  display: none;
}
</style>